var request = require('superagent');

var target = document.getElementById('submit');
target.addEventListener('click', send, false);

function send(){
    var num = document.getElementById('num').value;
    var num_str = String(num);
    alert(num_str);
    request
        .get('http://127.0.0.1:5000/execute/' + num_str)
        .end(function(err, res){alert(res);});
}

